.match-approval {
    max-width: 1000px;
    margin: 2rem auto;
    padding: 2rem;
}

.match-comparison {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
    margin: 2rem 0;
}

.original-person, .matched-person {
    background: white;
    padding: 2rem;
    border-radius: 12px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
    transition: transform 0.3s ease;
}

.match-comparison img {
    width: 100%;
    height: 350px;
    border-radius: 8px;
    transition: transform 0.4s ease;
}

.approval-actions {
    display: flex;
    gap: 1.5rem;
    justify-content: center;
    margin-top: 2rem;
}

.approve-btn, .reject-btn {
    padding: 1rem 2.5rem;
    border: none;
    border-radius: 8px;
    font-size: 1.1rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
}

.approve-btn {
    background: #4CAF50;
    color: white;
}

.reject-btn {
    background: #f44336;
    color: white;
}
.loading, .error, .not-found {
    text-align: center;
    padding: 2rem;
    font-size: 1.2rem;
}

.error {
    color: #f44336;
}
.date-info {
    font-size: 1rem;
    color: #666;
    margin: 0.5rem 0;
}

.reporter-info {
    font-size: 1rem;
    color: #444;
    font-weight: 500;
    margin: 0.5rem 0;
}